<template>
  <div class="wrapper">
    <img src="../../assets/img_wallet/photo@2x.png" alt />
    <div class="midTitle">
      <img src="../../assets/img_wallet/iOS@2x.png" alt />
      <p>SGU Wallet</p>
    </div>
    <div
      class="version"
    >{{$t('wallet.down.v')}}：{{version}}/{{$t('wallet.down.size')}}：{{size}}/{{$t('wallet.down.time')}}：{{updateTime}}</div>
    <div class="downser">{{$t('wallet.down.qr')}}</div>
    <div class="openurl">{{$t('wallet.down.open')}}：{{url}}</div>
    <div>
      <img :src="qrimg" alt />
    </div>
  </div>
</template>

<script>
import { getdownload } from "../../../api";

export default {
  mounted() {
    getdownload(1)
      .then(res => {
        this.version = res.data.data[0].teVersion;
        this.size = res.data.data[0].size;
        this.updateTime = res.data.data[0].updateTime;
        this.url = res.data.data[0].url;
        this.qrimg = res.data.data[0].qrCodeImage;
      })
      .catch(err => {
        window.console.log(err);
      });
  },
  data() {
    return {
      version: "",
      size: "",
      updateTime: "",
      url: "",
      qrimg: ""
    };
  }
};
</script>

<style lang="less" scoped>
.wrapper {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: space-around;
  align-items: center;
  padding: 1.71rem 0 1.51rem;
  & > img {
    width: 1.45rem;
    height: 1.64rem;
    margin-bottom: 0.63rem;
  }
  .midTitle {
    display: flex;
    align-items: center;
    height: 0.53rem;
    margin-bottom: 0.46rem;
    & > img {
      width: 0.42rem;
      height: 0.53rem;
      margin-right: 0.53rem;
    }
    p {
      color: #1679f4;
      font-size: 0.47rem;
    }
  }
  .version {
    color: #3d3d3d;
    font-size: 0.28rem;
  }
  .downser {
    margin-top: 0.61rem;
    margin-bottom: 0.45rem;
    color: #929292;
    font-size: 0.36rem;
  }
  .openurl {
    margin-bottom: 0.36rem;
    color: #92929f;
    font-size: 0.36rem;
  }
}
</style>